import * as d3 from 'd3'

export default class FancyParallelCoordinatesD3 {
    constructor(el, props) {
        this.update = this.update.bind(this);

        var margin = {top: 20, right: 0, bottom: 10, left: 0},
        width = props.dataWidth - margin.left - margin.right,
        height = props.height - margin.top - margin.bottom;

        parent = d3.select(el)
            .append("div")
            .style("width", props.width)
            .style("overflow-x", "scroll")
            .style("-webkit-overflow-scrolling", "touch");

        this.svg = parent.append('svg')
            .attr('width', width + margin.left + margin.right)
            .attr('height', height + margin.top + margin.bottom)
            .append('g')
            .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')');

        this.createPCP(props, this.svg);
    }

    update(props) {

    }

    createPCP(props, svg) {
        var dimensions = props.data.map(({label}) => label);
        var margin = {top: 20, right: 0, bottom: 10, left: 0},
        width = props.dataWidth - margin.left - margin.right,
        height = props.height - margin.top - margin.bottom;

        // Create axes for data
        var scales = {}
        for(let i = 0; i < dimensions.length; i++) {
            scales[dimensions[i]] = d3.scaleLinear()
                .domain(props.data[i].range)
                .range([height, 0])
        }

        // Create x-axes to lay out the y-axes
        var xScale = d3.scalePoint()
            .range([0, width])
            .padding(1)
            .domain(dimensions);

        // draw lines
        var values = [];
        props.data.map(function(d) {
            for(let i = 0; i < d.values.length; i++) {
                if(values.length < d.values.length) {
                    values.push({});
                }
                values[i][d.label] = d.values[i];
                values[i]['id'] = i;
            }
        });
        var foreground = svg.append("g")
            .selectAll("path")
            .data(values)
            .enter().append("path")
            .attr("d", path)
            .style("fill", "none")
            .style("stroke", function(d) {return d.id in props.selection ? "#005454" : "grey"})
            .style("opacity", 0.2)

        // Draw the axes
        var axes = svg.selectAll("myAxis")
            .data(dimensions).enter()
            .append('g')
            // move to right position
            .attr("transform", function(d) {return "translate(" + xScale(d) + ")";})
            // build axes
            .each(function(d) {d3.select(this).call(d3.axisLeft().scale(scales[d]));});
        axes
            .append("text")
            .style("text-anchor", "middle")
            .attr("y", -9)
            .text(function(d) {return d;})

        axes
            .selectAll("path")
            .style("stroke", "red");
        axes
            .selectAll("line")
            .style("stroke", "green");
        svg
            .selectAll("text")
            .style("fill", "purple")
            .style("font-size", props.fontSize);

        // brushing
        axes
            .each(function(d) {
                d3.select(this).call(scales[d].brush = d3.brushY()
                    .extent([[-10, 0], [10, height]])
                    .on("start", brush)
                    .on("brush", brush)
                    .on("end", brush)
                )
            })
            .selectAll("rect")
            .attr("x", -8)
            .attr("width", 16);

        var selections = new Map()

        function brush({selection}, key) {
            if(selection === null) selections.delete(key);
            else selections.set(key, selection.map(scales[key].invert));
            const selected = [];
            foreground.each(function(d) {
                var active = Array.from(selections).every(([key, [max, min]]) => d[key] >= min && d[key] <= max);
                d3.select(this).style("stroke", active ? "#005454" : "grey")
                    .style("opacity", active ? 0.5 : 0.2);
                if(active) {
                    d3.select(this).raise();
                    selected.push(d);
                    props.setProps({selection: selection})
                }
            });
        }

        // Reordering for axes
        var dragging = {};
        function position(d) {
            var v = dragging[d];
            return v == null ? xScale(d) : v;
        }
        axes.call(d3.drag()
            .on("start", function(event, d) {
                if(event.y > margin.top) {
                    return;
                }
                dragging[d] = this.__origin__ = xScale(d);
                this.__dragged__ = false;
            })
            .on("drag", function(event, d) {
                if(!this.__origin__) {
                    return;
                }
                dragging[d] = Math.min(width, Math.max(0, this.__origin__ += event.dx));
                foreground.attr("d", path);
                dimensions.sort(function(a, b) { return position(a) - position(b); });
                xScale.domain(dimensions);
                axes.attr("transform", function(d) { return "translate(" + position(d) + ")"; });
                this.__dragged__ = true;
            })
            .on("end", function(event, d) {
                if(!this.__dragged__) {
                    // no movement
                } else {
                    //reorder
                    d3.select(this).transition().attr("transform", "translate("+xScale(d) + ")");
                    // TODO: Add brush
                }
                xScale.domain(dimensions);
                delete this.__dragged__;
                delete this.__origin__;
                delete dragging[d];
            }))


        // Returns the path for a given data point.
        function path(d) {
          return d3.line()(dimensions.map(function(p) { return [xScale(p), scales[p](d[p])]; }));
        }
    }
}

